<template>
  <v-container fluid class="pa-0 pa-md-3">
    <v-list class="ly-list" v-if="playlists.length!==0">
      <template v-for="(playlist, index) of playlists">
        <v-list-item :key="playlist[0]">
          <v-row no-gutters>
            <v-col cols="12" md="10">{{ playlist[1] }}</v-col>
            <v-col cols="6" md="2" offset="6" offset-md="0" class="text-right">
              <v-btn icon @click.stop="onBtnDelClick(index)">
                <v-icon>mdi-delete-forever</v-icon>
              </v-btn>
              <v-btn icon :to="{name: 'playlist_form',query: {mode: 'edit', id: playlist[0]}}"
              >
                <v-icon>mdi-pencil</v-icon>
              </v-btn>
            </v-col>
          </v-row>
        </v-list-item>
        <v-divider :key="index" v-if="index!==(playlists.length-1)"></v-divider>
      </template>
    </v-list>
    <v-row class="centered-row" v-if="(playlists.length===0) && pageReady">
      <v-col cols="12" md="6">
        <v-card>
          <v-card-title>
            Belum ada playlist!
          </v-card-title>
          <v-card-text>
            Tambahkan playlist dengan meng-klik tombol yang ada di sudut 
            kanan-bawah layar!
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-btn fab fixed bottom right dark color="pink"
      :to="{name: 'playlist_form', query: {mode: 'new'}}"
    >
      <v-icon>mdi-plus</v-icon>
    </v-btn>
    <v-dialog max-width="300" v-model="dlgConfirmDel">
      <v-card>
        <v-card-title>Konfirmasi</v-card-title>
        <v-card-text>
          Hapus {{ playlistNameToBeDel }}?
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="dlgConfirmDel=false"
            :disabled="loading"
          >Batal</v-btn>
          <v-btn text color="warning" :loading="loading" @click="deletePlaylist"
          >Ok</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
"use strict"

import { API_URL } from '@/config'
import { deleteWithAuth, getWithAuth } from '@/lib/fetch_utils'

export default {
  name: "Playlists",
  data: () => ({
    playlists: [],
    dlgConfirmDel: false,
    indexToBeDel: -1,
    playlistNameToBeDel: '',
    loading: false,
    pageReady: false
  }),
  watch: {
    loading: function() {
      this.$store.commit('SET_LOADING', this.loading)
    },
  },
  created() {
    this.$store.commit("SET_TOOLBAR", {title:"Playlists"})
    this.loadData()
  },
  methods: {
    loadData: async function() {
      this.loading = true
      let resp = await getWithAuth(`${API_URL}/playlists`)
      this.loading = false
      this.pageReady = true
      if (200 !== resp.code) {
        this.$store.commit('SHOW_APP_ALERT', {
          title: 'Error',
          text: resp.message
        })
        return
      }
      this.playlists = resp.data.rows
    },
    onBtnDelClick(index) {
      this.indexToBeDel = index
      this.playlistNameToBeDel = this.playlists[index][1]
      this.dlgConfirmDel = true
    },
    deletePlaylist: async function() {
      let playlistId = this.playlists[this.indexToBeDel][0]
      this.loading = true
      let resp = await deleteWithAuth(`${API_URL}/playlists/${playlistId}`)
      this.loading = false
      this.dlgConfirmDel = false
      if (200 !== resp.code) {
        this.$store.commit('SHOW_APP_ALERT', {
          title: 'Error',
          text: resp.message
        })
        return
      }
      this.playlists.splice(self.indexToBeDel, 1)
    }
  }
}
</script>

<style scoped>
a {
  text-decoration: none;
}

.ly-list {
  margin-bottom: 64px;
}
</style>
